.add-profile {
  background: #edeff6;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content min-content;
  min-height: 503px;
  border-radius: 0 0 28px 28px;
  z-index: 4;
  position: relative;
  margin-top: -52px;
  padding: 90px 25px 38px 49px;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    padding: 128px 75px 85px 130px;
    display: flex;
    flex-direction: column;
    border-radius: 0 0 55px 55px;
  }

  @media (min-width: @desktop-width) {
    padding: 159px 133px 187px 220px;
  }
}

.add-profile img {
  width: 100px;
  height: 100px;
  margin-left: auto;

  @media (min-width: @tablet-width) {
    margin: 0;
    position: relative;
    top: 290px;
    left: 453px;
    z-index: 2;
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    width: 198px;
    height: 198px;
    right: 132px;
    left: auto;
    top: 337px;
  }
}

.add-profile a {
  font-weight: 700;
  color: #132576;
  text-decoration: none;
  grid-column: ~"1 / 3";
}

.add-profile__show {
  font-size: 16px;
  line-height: 16px;
  display: flex;
  align-items: center;
  margin-top: 27px;
  position: relative;

  @media (min-width: @tablet-width) {
    margin-top: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

.add-profile__show::before {
  @media (min-width: @tablet-width) {
    content: "";
    position: absolute;
    width: 68px;
    height: 68px;
    left: -89px;
    bottom: -22px;
    background: url("../img/icon/add-profile-btn.png") no-repeat;
  }
}

.add-profile__close {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  width: 100%;
  height: 59px;
  margin-bottom: -33px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0 20px 20px;
  background: #f3f4f8;

  @media (min-width: @tablet-width) {
    font-size: 24px;
    line-height: 24px;
    text-transform: none;
  }
}

.add-profile h2 {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #ff8d30;
  padding-top: 14px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
    padding-left: 12px;
    font-size: 45px;
    line-height: 45px;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  @media (min-width: @desktop-width) {
    text-align: center;
    margin-left: -81px;
    padding-left: 0;
    margin-bottom: 160px;
  }
}

.add-profile__list {
  list-style: none;
  padding: 0;
  margin: 0;
  grid-row: 2;
  grid-column: ~"1 / 3";
  margin-top: 29px;

  @media (min-width: @tablet-width) {
    padding-bottom: 57px;
    margin-top: -29px;
  }

  @media (min-width: @desktop-width) {
    width: 813px;
  }
}

.add-profile__item {
  width: 200px;
  font-weight: 300;
  font-size: 16px;
  line-height: 16px;
  color: #444444;
  border-top: 1px solid #e1e3e9;
  padding: 13px 0 15px 0;
  position: relative;

  @media (min-width: @tablet-width) {
    width: 563px;
    padding: 20px 0 18px 0;
    display: flex;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    width: 813px;
  }
}

.add-profile__item:last-child {
  margin: 0;
  border-bottom: 1px solid #e1e3e9;
}

.add-profile__item:first-child::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 34px;
  right: -45px;
  top: 2px;
  background: url("../img/icon/number_1.png") no-repeat;

  @media (min-width: @tablet-width) {
    width: 68px;
    height: 68px;
    left: -90px;
    top: auto;
    background: url("../img/icon/number_1-tablet.png") no-repeat;
  }
}

.add-profile__item:nth-child(2)::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 34px;
  right: -45px;
  top: 2px;
  background: url("../img/icon/number_2.png") no-repeat;

  @media (min-width: @tablet-width) {
    width: 68px;
    height: 68px;
    left: -90px;
    top: auto;
    background: url("../img/icon/number_2-tablet.png") no-repeat;
  }
}

.add-profile__item:last-child::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 34px;
  right: -45px;
  top: 2px;
  background: url("../img/icon/number_3.png") no-repeat;

  @media (min-width: @tablet-width) {
    width: 68px;
    height: 68px;
    left: -90px;
    top: auto;
    background: url("../img/icon/number_3-tablet.png") no-repeat;
  }
}

.add-profile__item p {
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 28px;
    line-height: 28px;
  }
}

.add-profile__item p:last-child {
  margin-top: 5px;
  margin-left: auto;

  @media (min-width: @tablet-width) {
    margin-top: 0;
    position: relative;
    z-index: 2;
  }
}

.add-profile__buisnes {
  display: none;
  border-radius: 20px;
  position: absolute;
  top: 85px;
  left: 27px;
  min-width: 270px;
  min-height: 443px;
  // display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #ffffff;
}

.add-profile__buisnes h2 {
  text-align: center;
}

.add-profile__list--buisnes {
  padding: 0;
  margin-left: 25px;
  background-color: #ffffff;
  box-sizing: border-box;
}

.add-profile__list--buisnes li {
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  padding: 11px 0;
  width: 219px;
  margin: 0;
}

.add-profile__list--buisnes li:last-child {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.add-profile__list--buisnes li::before {
  display: none;
}

.add-profile__background {
  @media (min-width: @tablet-width) {
    background: #e2e5f2;
    width: 198px;
    height: 285px;
    border-radius: 20px;
    margin-top: -285px;
    position: relative;
    left: 401px;
    top: 217px;
    z-index: 1;
  }

  @media (min-width: @desktop-width) {
    width: 446px;
    height: 300px;
    margin-top: -300px;
    left: 524px;
    top: 250px;
  }
}
